import axios from 'axios'
//const baseUrl = 'https://manage-items.herokuapp.com/api/items'
const baseUrl = '/api/items'

let token = null

const getAll = () => {
  return axios.get(baseUrl)
}

const setToken = (newToken) => {
  token = `bearer ${newToken}`
}

const create = async (newObject) => {
  /*const config = {
    headers: { 'Authorization': token
  }
  return axios.post(baseUrl, newObject, config)
  }*/
  const response = await axios.post(baseUrl, newObject)
  console.log(response.data)
  return response
}

const updateUsage = async (id, newObject) => {
  /*const config = {
    headers: { 'Authorization': token }

    const response = await axios.post(`${baseUrl}/${id}/usage`, newObject, config)
  }*/

  const response = await axios.post(`${baseUrl}/${id}/usage`, newObject)
  return response.data
}

const update = (id, newObject) => {
  /*const config = {
    headers: { 'Authorization': token }
  }

  */
  const request = axios.put(`${baseUrl}/${id}`, newObject)
  return request.then(response => response.data)
}

const remove = (id) => {
  const config = {
    headers: { 'Authorization': token }
  }
  return axios.delete(`${baseUrl}/${id}`, config)
}

export default { getAll, create, update, remove, updateUsage, setToken }
