import React, {useState, useEffect} from 'react';
//import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom'

import logo from './logo.svg';
import './App.css';
import itemService from './services/items';
import ItemList from './components/ItemList';
import ItemForm from './components/ItemForm';
import FilterInput from './components/FilterInput';

const App = (props) => {

  const [newItem, setNewItems] = useState([])
  const [items, setItems] = useState([])
  const [visibilityFilter, setVisibilityFilter] = useState('')

  useEffect(() => {
    const response = itemService.getAll().then(response=> {
      setItems(response.data)
    })
  }, [])

  console.log(items)
  if (items===undefined) {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
      </div>
    )
  }
  const itemsToShow = items.filter(item => item.name.toLowerCase().indexOf(visibilityFilter.toLowerCase()) !== -1)
  const handleFilterChange = (event) => {
    setVisibilityFilter(event.target.value)
  }

  const handleUsageIncrease = async (id, newUsage) => {
    const item = items.find(item => item.id === id)
    const changedItem = {...item, usage: item.usage.concat([{"date": new Date().toISOString(),"amount":Number(newUsage)}])}
    console.log(changedItem.usage.map(u=>u))
    await itemService.update(id, changedItem)
    setItems(items.map(item => item.id !== id ? item : changedItem))
  }


  const addItem = async (newItem) => {
    try {
      const response = await itemService.create(newItem)
      setItems([...items, response.data])
    } catch (error) {
      alert(`could not add '${newItem.name}' to items`)
      }
  }

  //console.log(new Date().toISOString())

  /*const addNote = (event) => {
    event.preventDefault()
    const itemObject = {
      name: newItem.name,
      unit: newItem.unit,
      usage: newItem.usage
    }
  }*/

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <input onChange={handleFilterChange} value={visibilityFilter}/>
        <ItemList items={itemsToShow} handleIncrease={handleUsageIncrease}/>
        <ItemForm handleAddItem={addItem}/>
      </header>
    </div>
  );
}

export default App;
