import React, {useState} from 'react'
import '../index.css'

const ItemForm = (props) => {

  const [newItem, setNewItem] = useState([])
  const [showForm, setShowForm] = useState(false)
  const [selectedUnits, setSelectedUnits] = useState('')
  const [commonUsages, setCommonUsages] = useState([])
  const [newCommonUsage, setNewCommonUsage] = useState('')

  const handleShowMore = () => {
    setShowForm(!showForm)
  }

  const handleUnitChange = (event) => {
    const units = event.target.value
    setSelectedUnits(units)
  }

  const handleNewCommonUsageChange = (event) => {
    const usage = event.target.value
    setNewCommonUsage(usage)
  }

  const addCommonUsage = (event) => {
    const usage = newCommonUsage

    setCommonUsages(commonUsages.concat(usage))
    setNewCommonUsage('')
  }

  const clearCommonUsages = () => {
    setCommonUsages([])
  }

  const addNewItem = (e) => {
      try{
        e.preventDefault()
        const newItem = {
          name: e.target.name.value,
          units: e.target.units.value,
          usage: [],
          common_usage: commonUsages
        }

        props.handleAddItem(newItem)

        e.target.name.value = ''
        e.target.units.value = ''
        setNewCommonUsage('')
        setCommonUsages([])

        /*const notification= `a new blog ${newBlog.title} by ${newBlog.author} was added`
        this.props.notify(notification, 'success', 5)
        } catch (ex) {
          const error = `couldn't add new blog due to an error: ${ex}`
          this.props.notify(error, 'error', 5)
        }*/
      } catch (ex) {
          const error = `couldn't add new item due to an error: ${ex}`
          alert(error)
        }
      }

  const showMore = { display: showForm ? '' : 'none' }
  const showDistance = { display: selectedUnits==='km' ? '' : 'none' }
  const showTime = { display: selectedUnits==='h' ? '' : 'none' }
  const displayCommonUsages = { display: commonUsages.length === 0 ? 'none' : '' }
  const mockButton = {display: newCommonUsage === '' ? 'none' : '', backgroundColor: "coral"}
  const clearButton = {display: commonUsages.length > 0 ? '' : 'none', backgroundColor: "teal"}

  return(
    <div>
    <div onClick={handleShowMore} style={{cursor: "pointer"}}>
      <h3>Add new item</h3>
    </div>
    <div style={showMore}>
      <form onSubmit={addNewItem}>
        name: &emsp;
        <input
          type="text"
          name="name"
        />
        <br/><br/>
        What do you measure?
        <br/>
        <input onChange={handleUnitChange} type="radio" id="km" name="units" value="km"/>
        distance &nbsp;
        <input onChange={handleUnitChange} type="radio" id="h" name="units" value="h"/>
        time
        <br/><br/>
        Add common usecases for the item
        <br/>
        <input onChange={handleNewCommonUsageChange} value={newCommonUsage} type="number"/>
        <p style={mockButton} onClick={addCommonUsage}>Add</p>

        <p style={displayCommonUsages}>[{commonUsages.map(u=>
          <a> {u}{selectedUnits} </a>
        )}] &emsp; <a style={clearButton} onClick={clearCommonUsages}> clear </a> </p>
        <br/><br/>
        <button>Add a new item</button>
        <br/><br/>
        </form>
      </div>
      </div>
  )
}



export default ItemForm
